.homePagePost {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    border-radius: 10px;
    border: 1px solid lightgrey;
}

.homePagePostHeader {
    display: flex;
    align-items: center;
    padding: 0.75rem;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    background-color: white;
}

.homePagePost > img {
    object-fit: contain;
    width: 100%;
    height: auto;
    background-color: white;
}

.homePagePostFooter {
    padding-top: 0.5rem;
    padding-left: 0.75rem;
    background-color: white;
}

.homePagePostActionButtons {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
}

.homePagePostActionButtons > button {
    background: none;
    border: none;
    font-size: 1.75rem;
}

.homePagePostActionButtons > button > #commentIcon {
    cursor: pointer;
}

.homePagePostActionButtons > button > #heartIcon {
    cursor: pointer;
}

.homePagePostLikes {
    display: flex;
    align-items: center;
    background-color: white;
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
}

.homePagePostLikes > p {
    font-size: 14px;
    margin-left: 0.25rem;
}

.homePagePostTitle {
    font-size: 14px;
    margin-bottom: 0.25rem;
}

.homePagePostComments {
    color: #8e8e8e;
    font-size: 14px;
    text-decoration: none;
    background: none;
    border: none;
    cursor: pointer;
}

.homePagePostDate {
    color: #8e8e8e;
    font-size: 10px;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    text-transform: uppercase;
}

.homePagePostComment {
    display: flex;
    align-items: center;
    padding-bottom: 0.5rem;
    background-color: white;
    border-top: 1px solid rgba(239,239,239);
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;

}

.homePagePostComment > input {
    padding: 0.75rem 0.5rem 0.5rem;
    border: none;
    outline: none;
    flex: 1;
}
